  .saves-main,
  .history-main,
  .basket-main {
    position: absolute;
    top: 170px;
    left: 350px;
    opacity: 0;
    margin-left: -10000px;
    transition: opacity 0.5s ease; 
  }
      
  .saves-main.active,
  .history-main.active,
  .basket-main.active {
    background-color: #fff;
    opacity: 1;
    margin-left: 0;
  }