div h1 {
    text-transform: uppercase;
    font-size: 24px;
    margin: 30px 65px;
    font-weight: 500;
}  
div aside {
    padding: 20px;
    width: 20%;
    margin-left: 3%;
    float: left;
    margin-right: 30px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
  }
div aside p {
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 20px;
  }
div aside nav {
    display: flex;
    flex-direction: column;
  }
div aside nav a {
    text-transform: uppercase;
    text-decoration: none;
    color: #5c5c5c;
    border-bottom: 1px solid #dfdfdf;
    padding: 15px 0;
    transition: all 500ms ease;
  }
div aside nav a:hover {
    color: #000;
    text-decoration: underline;
    background-color: #f6f6f6;
  }
  
div .bread_crumbs {
    display: flex;
    margin-bottom: 20px;
  }
div .bread_crumbs a {
    text-decoration: none;
    color: #5c5c5c;
    
  }
div .bread_crumbs p {
    margin: 0;
}
div .bread_crumbs a:hover {
    text-decoration: underline;
  }